.all-tasks {
  background-color: transparent;
  padding: 0px;
  margin-top: 1em;
}

.task-list {
  .task-card {
    display: block;
    width: 90%;
    margin: 5%;
    padding-left: 0.5em;
    background-color: rgb(201, 112, 112);

    .task-name {
      text-align: left;
      font-size: 1em;
      width: 90%;
      margin: 5%;
      padding-left: 0.5em;
      background-color: white;
      line-height: 2.7em;
      border-radius: 5px;
    }
  }
}

.task-hero {
  display: inline-table;
  width: 70%;
  border: 0px solid red;
}
.task-toolbar {
  display: inline-table;
  width: 25%;
  text-align: right;
  border: 0px solid blue;
  padding: 0.1rem;
  float: right;
}
.hero-text {
  font-size: 24px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  padding: 0 0 0 1rem;
}

.card {
  position: relative;
  margin: 1rem;
  background: white;
  box-shadow: 2px 4px 25px rgba(0, 0, 0, 0.1);
  border-radius: 12px;
  overflow: hidden;
  transition: all 0.3s linear;
  border: none;
  h1 {
    text-align: center;
    padding: 1.5rem 2.5rem;
    background-image: linear-gradient(120deg, #fbc2eb 0%, #a6c1ee 100%);
    margin: 0 0 2rem 0;
    font-size: 1.5rem;
    color: white;
  }
}

.card:hover {
  box-shadow: 2px 8px 45px rgba(0, 0, 0, 0.25);
}

.card-form-div {
  border: 3px solid yellow;
}

.card p {
  padding: 0 2em;
  margin: 0;
}

.btn-icon {
  box-sizing: content-box;
  width: 1em;
  height: 1em;
  padding: 0.5em 0.5em;
  color: #ffffff;
  background-color: #000000;
  border: 0;
  border-radius: 1rem;
  cursor: pointer;
  margin: 0.1rem;
}

